@import "../../scss//utils/variables";
@import "../../scss//utils/mixins";

.account {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid black;
  background-color: #fff;
  width: 80%;
  margin: 0 auto;
  flex-direction: column;
  padding: 1.5rem;
  box-sizing: border-box;
  text-align: left;
  margin-bottom: 2rem;

  @include breakpoint(desktop) {
    flex-direction: row;

    .transaction-button {
      max-width: 200px;
    }
  }

  .contentWrapper {
    width: 100%;
    @include breakpoint(desktop) {
      width: auto;
    }
  }

  .title {
    margin: 0;
    padding: 0;
    font-size: 1rem;
    font-weight: normal;
  }

  .amount {
    margin: 0;
    font-size: 2.5rem;
    font-weight: bold;
  }

  .description {
    margin: 0;
  }
}
